<template>
	<div>
		<inputBtn :val='value' v-on:getChangeVal="changeVal($event)"></inputBtn>
	</div>
</template>

<script>
	import inputBtn from '@/components/input.vue';
	
	
	export default {
		name:'userEdit',
		data() {
			return {
				value:'1'
			}
		},
		methods: {
			changeVal(val) {
				this.value ++;
				console.log(val)
			}
		},
		
		components: {
			inputBtn
		},
	}
</script>

<style scoped>

</style>


